<script lang="ts" module>
	import type { HTMLAttributes } from '../../../internal/html-attributes.js';
	import type { PropsWithElement } from '../../../internal/props-with-element.js';

	export interface SliderHiddenInputProps extends PropsWithElement<'input'>, HTMLAttributes<'input', 'children'> {}
</script>

<script lang="ts">
	import { RootContext } from '../modules/root-context.js';
	import { ThumbContext } from '../modules/thumb-context.js';
	import { mergeProps } from '@zag-js/svelte';

	const props: SliderHiddenInputProps = $props();

	const slider = RootContext.consume();
	const thumbProps = ThumbContext.consume();

	const { element, ...rest } = $derived(props);

	const attributes = $derived(mergeProps(slider().getHiddenInputProps(thumbProps()), rest));
</script>

{#if element}
	{@render element(attributes)}
{:else}
	<input {...attributes} />
{/if}
